import React from 'react'
import { Spring } from 'react-spring/renderprops'

export default function () {
    return (
        <Spring
            from={{opacity:0, marginTop: -500}}
            to={{opacity: 1, marginTop: 0}}
        >
            {props => (
               <div style={props}>
                  <div style={c1Style1} >
                    <h1>Component 1</h1>
                    <p>lorem lorem lorem lorem lorem lorem lorem aaa aaa
                    lorem lorem lorem lorem lorem lorem lorem aaa aaa
                    lorem lorem lorem lorem lorem lorem lorem aaa aaa
                    lorem lorem lorem lorem lorem lorem lorem aaa aaa
                    lorem lorem lorem lorem lorem lorem lorem aaa aaa
                    </p>
                    <Spring
                        from={{number:0}}
                        to={{number:10}}
                        config = {{duration: 5000}}
                    >
                        {props => (
                            <div style={props}>
                               <h1 style={counter}>
                                {props.number.toFixed()}
                               </h1>
                            </div>

                        )}                   
                        
                    </Spring>
                  </div>
                </div>
            )}

        </Spring>
       
    )
}

const c1Style1 = {
    background: "steelblue",
    color: "white",
    padding: "1.5rem"
}


const counter = {
    background: "#333",
    textAlign: "center",
    width: "100px",
    borderRadius: "50%",
    margin: "1rem auto",
}