import React, { Component } from 'react';
import { Spring } from 'react-spring/renderprops'

export class Component2 extends Component {
    render() {
        return (
            <Spring
            from={{opacity:0}}
            to={{opacity: 1}}
            config={{delay: 1000, duration: 1000}}
        >
            {props => (
               <div style={props}>
                  <div style={c2Style1} >
                    <h1>Component 2</h1>
                    <p>lorem lorem lorem lorem lorem lorem lorem aaa aaa
                    lorem lorem lorem lorem lorem lorem lorem aaa aaa
                    lorem lorem lorem lorem lorem lorem lorem aaa aaa
                    lorem lorem lorem lorem lorem lorem lorem aaa aaa
                    lorem lorem lorem lorem lorem lorem lorem aaa aaa
                    </p>
                    <button style={btn} onClick={this.props.toggle}>                  
                    Toggle Component3
                    </button>
                  </div>
                </div>
            )}

        </Spring>
        )
    }
}

const c2Style1 = {
    background: "slateblue",
    color: "white",
    padding: "1.5rem"
}

const btn = {
    background: "#333",
    color: "#fff",
    padding: "1rem 2rem",
    border: "none",
    textTrasnform: "uppercase",
    martin: "15px 0"
};

export default Component2
